@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-soothing: antialiased;
}

input, button, textarea{
    font: 400 18px Roboto, sans-serif;
}

button{
    cursor: pointer;
}

.main-container{
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
    text-align: center;
}

.main-container h1{
    margin-top: 24px;
}
.main-container p{
    margin-top: 24px;
    font-size: 18px;
}

.main-container ul{
    list-style: none;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
}
.main-container ul li{
    background: rgb(155, 247, 119);
    border: solid 1px rgb(136, 216, 105);
    padding: 10px 30px;
    font-size: 25px;
}

.main-container ul li a{
    text-decoration: none;
}


.easy-container{
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
}

.easy-container header {
    display: flex;
    align-items: center;
    justify-content: center;
}

.easy-container .configuration-group{
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.easy-container .configuration-group label{
    color: rgb(73, 73, 73);
    margin-right: 10px;
    font-size: 18;
}

.easy-container .configuration-group input{
    padding: 5px 10px;
    margin-right: 40px;
}

.easy-container h1{

}

.easy-container button{
    margin: 24px auto 0 auto;
    border: rgb(52, 72, 187) 2px solid;
    background: rgb(52, 115, 187);
    padding: 10px 30px;
    text-align: center;
    color: #fff;
    border-radius: 8px;
    display: block;
}

.easy-container button:hover{
    opacity: 0.8;
}

.easy-container .words{
    max-width: 80%;
    margin: 24px auto auto;
    text-align: center;
}
.easy-container .words span{
    margin: 0 10px;
    font-size: 24px;
    line-height: 28px;
    cursor: default;
    display: inline-block;
}

.easy-container .words span.finded{
    text-decoration: line-through;
}

.easy-container .word-board{
    width: 100%;
    margin-top: 24px;
}

.easy-container  .word-board ul{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.easy-container  .word-board ul li{
    font-size: 16px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
    line-height: 50px;
    width: 50px;
    height: 50px;
    text-align: center;
    text-transform: uppercase;
}

.easy-container  .word-board ul li.selected{
    background: rgb(122, 122, 122);
    color: #fff;
}

.easy-container  .word-board ul li.selected:hover{
    background: rgb(122, 122, 122);
    color: #fff;
}

.easy-container  .word-board ul li:hover{
    background: #ccc;
}



